.App {
}

.container {
  height: 100%;
  width: 100%;
}

.projects {
  padding: 16px 0;

  &__grid-container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
  }
}

.clear {
  clear: both;
}

.btn {
  width: auto;
  font-size: 1em;
  color: #b91b9c;
  padding: 5px 10px;
  min-width: 100px;

  &__border-radius {
    border-radius: 2px;
  }

  &__bright {
    font-size: 5em;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin-bottom: 1em;
  }

  &__dark {
    color: var(--app-white);
    background: var(--text-highlight-darker);
    min-width: 134px;
    border: 2px solid var(--text-highlight-darker);

    &:hover {
      cursor: pointer;
      background-color: var(--text-highlight);
      border: 2px solid var(--text-highlight);
    }
  }

  &__light,
  &__light-wide {
    color: var(--text-highlight-darker);
    background: var(--app-white);
    min-width: 134px;
    border: 2px solid var(--text-highlight-darker);

    &:hover {
      cursor: pointer;
      color: var(--app-white);
      background-color: var(--text-highlight-darker);
      border: 2px solid var(--text-highlight-darker);
    }
  }
}

