.project h2 {
  text-align: center;
}

.project__content {
  max-width: 900px;
  padding: 0 16px;
  margin: auto;
}

@media (min-width: 768px) {
  .projects__grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1200px) {
  .projects__grid-container {
    grid-template-columns: repeat(3, 1fr);
  }

  .container {
    max-width: 1200px;
    padding: 0 16px;
    margin: auto;
  }

  .btn-dark,
  .btn-light {
    font-size: 1em;
    min-width: 150px;
  }
}
